import React, { useState } from 'react'
import "./TopNav.css"
import { useNavigate } from "react-router-dom"
import { display } from '@mui/system';

const TopNav = () => {

    const navigate = useNavigate();
    const [display, setdisplay] = useState("none");

    const login_option =()=>{
        setdisplay("block");
    }

    const login_option2 =()=>{
        display = "none";
    }

    return (
        <div className='top-nav-wrapper'>
            <form className='login_option' style={{display: display}}>
                <br />
                <h2>Choose Login Option</h2>
                <br /><br />
                <button className='login_option_btn' onClick={() => navigate("/login")}>Costumer Login</button>
                <br /><br />
                <button className='login_option_btn'>Shop Keeper Login</button>
                <br /><br />
                <button className='login_option_btn2' onClick={login_option2}>Go Back</button>
            </form>
            <div className="top-mail-wrapper">
                <a className='top-mail' href="mailto:shoppixo.shopping@gmail.com">shoppixo.shopping@gmail.com</a>
            </div>
            <div className="top-main-menus-wrapper">
                <p className="t-menus" onClick={() => navigate("/")}>Home</p>
                <p className="t-menus">Shop</p>
                <p className="t-menus">Pages</p>
                <p className="t-menus">Blog</p>
                <p className="t-menus">Account</p>
                <p className="t-menus">Help</p>
            </div>
            <div className="top-right-menus">
                {/* <p className="t-r-menus" onClick={() => navigate("/login")}>Login</p> */}
                <p className="t-r-menus" onClick={login_option}>Login</p>
                
                <div className="top-cart-wrapper">
                    <p className="top-cart-btn">Cart</p>
                </div>
            </div>
        </div>
    )
}

export default TopNav