import React from 'react'
import "./ProductCard.css"
import AddCircleOutlineIcon from "@mui/icons-material/AddCircleOutline";
import RemoveCircleOutlineIcon from "@mui/icons-material/RemoveCircleOutline";

const ProductCard = (props) => {

    const { image, title, description, price, cardSettings, } = props;
    // const { buttonsHeight, allowButtons } = cardSettings;

    console.log("ProductCard--","image", image, "title", title, "description", description);

    return (
        <div className="product-card-wrapper" style={{ height: cardSettings?.buttonsHeight || cardSettings?.allowButtons ? '355px' : '320px' }}>
            <div className="product-card-image-box">
                <img className="product-card-image" src={image} alt="" />
            </div>
            <div className="product-card-body">
                <h5 className="prod-title p-hover">{title}</h5>
                <p className="prod-description p-hover">{description}</p>
                <p className="prod-price">{parseFloat(price ? price : 0).toFixed(2)}</p>
            </div>
            {
                cardSettings?.allowButtons && (
                    <div className="prod_cart_option d-flex justify-content-between">
                        <div className="buttons_opt">
                            <RemoveCircleOutlineIcon
                                className="m_btn"
                            />
                            <span className="cart_item">
                                {1}
                            </span>
                            <AddCircleOutlineIcon
                                className="m_btn"
                            />
                        </div>
                        <div className="cart_btn_div">
                            <span className={"btn cart_btn"}>
                                {"Add To Cart"}
                            </span>
                        </div>
                    </div>
                )
            }

        </div>
    )
}

export default ProductCard