import React from 'react'
import "./NewArrivalProducts.css"
import { NewArrivalProductsList } from "../../../../database/HomeDB"
import ProductCard from '../../../../components/ProductCard';
import { Col, Row } from 'reactstrap';

const NewArrivalProducts = () => {

    return (
        <>
            {
                NewArrivalProductsList && NewArrivalProductsList.length > 0 && (
                    <div className='new-arrival-products-wrapper'>
                        <div className="new-arrival-products-header">
                            <h5 className='new-arrival-head'>NEW ARRIVAL <span className='new-arrival-text'>Recently added our store</span></h5>
                            <span className="new-arrival-view_all">View All</span>
                        </div>
                        <Row>
                            {
                                NewArrivalProductsList.map((item) => (
                                    <Col className="col_mb_4_not_last" xs="12" sm="6" md="4" lg="3">
                                        <ProductCard
                                            image={item.image}
                                            title={item.title}
                                            description={item.description}
                                            price={item.price}
                                        />
                                    </Col>
                                ))
                            }
                        </Row>

                    </div>
                )
            }
        </>
    )
}

export default NewArrivalProducts