/* eslint-disable no-unused-vars */
import React from 'react'
import "./Home.css"
import NavSearch from '../../components/Header/NavSearch'
import TopNav from '../../components/Header/TopNav'
import HomeSlider from "../../components/HomeSlider"
import Categories from '../../components/Categories'
import { AdsInfoFlashBox } from "../../components/ElementsUi"
import ProductsAdsPage from '../ProductsAdsPage'
import { AddsFlash_List, homePagesProducts } from "../../database/HomeDB"
import HomeProductsDeals from './Contents/HomeProductsDeals'
import NewArrivalProducts from './Contents/NewArrivalProducts'

const Home = () => {

    const MenFashion = "images/products/men-fashion.jpeg"

    return (
        <div className='home-wrapper'>
            {/* <TopNav />
            <NavSearch /> */}
            <div className="hero-section-wrapper">
                <div className="hero-categories">
                    <Categories />
                </div>
                <div className="hero-sliderbox">
                    <HomeSlider />
                </div>
            </div>
            <div className="row mt-5 app_container">
                {
                    AddsFlash_List.map((item) => (
                        <div className="col-sm-6 col-md-4 col-lg-3 mb-3">
                            <AdsInfoFlashBox
                                fullwidth
                                image={`${process.env.PUBLIC_URL}/${item.image}`}
                                title={item.title}
                                description={item.description}
                            />
                        </div>
                    ))
                }
            </div>
            <div className="app_container">
                {
                    homePagesProducts.map((item => (
                        <ProductsAdsPage
                            adsFlashList={item.adsFlashList}
                            productList={item.productList}
                            cardSettings={item.cardSettings}
                        />
                    )))
                }
            </div>
            <div className="app_container">
                <HomeProductsDeals />
            </div>
            <div className="app_container">
                <NewArrivalProducts />
            </div>
        </div>
    )
}

export default Home